import React, { useEffect, useState } from 'react';
import { Line } from 'react-chartjs-2';
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
} from 'chart.js';
import { useNavigate } from 'react-router-dom';
import WarningsContainer from '../../routes/pages/Common/Warnings/WarningsContainer';
import WarningsPresenter from '../../routes/pages/Common/Warnings/WarningsPresenter';

// Chart.js    
ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend
);

const Graphe = () => {
  const[selectMonth, setSelectMonth]= useState(null);
  const navigate = useNavigate();

  const dummy = {
    "positions": [
      {
        "image": ["images/1.png", "images/2.png"],
        "month": 5
      },
      {
        "image": "images/3.png",
        "month": 6
      },
    ]
  };



  const data = {
    labels: ['5', '6'],
    datasets: [
      {
        label: '  ',
        data: [2, 1], // 5: 2, 6: 1
        borderColor: 'rgba(75, 192, 192, 1)',
        backgroundColor: 'rgba(75, 192, 192, 0.2)',
        borderDash: [5, 5], //  
      },
    ],
  };

  const options = {
    responsive: true,
    plugins: {
      legend: {
        position: 'top',
      },
      title: {
        display: true,
        text: '  ',
      },
    },
    onClick: () => navigate('/datewarnings/1')
    
    
    // onClick: (idx) => {
    //   const find = dummy.positions.map(idx)
    //   console.log(dummy.positions[idx].month)
    // },
  };

  return (
    <div style={{ width: '400px', height: '200px', margin: '0 auto' }}>
      <Line data={data} options={options} />
    </div>
  );
};

export default Graphe;
