import React, { useState, useEffect } from "react";

/* URL    */
import { Route, Routes, useFetcher } from 'react-router-dom';

/*       . */
import { Datewarnings, Main, Warnings } from "./pages";
import { MainLayout } from "../Layout";

/*     . */
const Router = () => {
    /* (state)  useEffect  */
    const test = useState(0); //   state

    /*   useEffect */
    useEffect(() => {

    }, []); // []        

    /*     */
    return (
        /*   return     . */
        <div className="app">
                <Routes>
                    {/* 
                    path: URL
                        * path="/user"  http://localhost:3000/user  
                    element:  
                        *  import   
                */}
                    <Route
                        path="/"
                        /*
                              
                            *   Ctrl   App      .
                            *   Command   App      .
                        */
                        element={<Main />}
                    />
                    <Route
                        path="/warnings/:id"
                        element={<Warnings />}
                    />
                    <Route
                        path="/datewarnings/:id"
                        element={<Datewarnings />}
                    />
                </Routes>
        </div>
    )
}

/* App/App.js import   */
export default Router;